import {mapState, mapGetters} from "vuex";
export default {
    name: "app-order-share-modal",
    data() {
        return {
            show: true,
        };
    },
    computed: {
        ...mapState({
            share_setting: state => state.mallConfig.share_setting,
            custom_setting: state => state.mallConfig.share_setting_custom,
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme'
        })
    }
}
